import WhosComing from './WhosComing';

const { registerBlockType } = wp.blocks;
const { withAPIData } = wp.components;

const getCarbonFields = () => ({ options: '/carbon-fields/v1/options' });

registerBlockType('colbycomms/whos-coming', {
  title: "Who's Coming",
  icon: 'universal-access-alt',
  category: 'layout',
  edit: () => (
    <div>
      Who's Coming (a listing of people attending an event). Preview this page
      to see it in action.
    </div>
  ),
  save: async () => {
    console.dir(
      withAPIData(getCarbonFields)(props => <WhosComing {...props} />)
    );
  },
});



