import Papa from 'papaparse';

import Person from './Person';

class WhosComing extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      data: '',
      people: [],
      keys: [],
    };
  }

  componentWillReceiveProps(nextProps) {
    this.maybeProcessReceivedData(nextProps);
  }

  addPeopleToState = () =>
    new Promise(resolve => {
      const data = Papa.parse(this.state.data).data;
      const keys = data.shift();

      const people = data.reduce((output, line) => {
        const obj = {};
        for (var i = 0; i < line.length; i += 1) {
          obj[keys[i]] = line[i];
        }
        return output.concat([obj]);
      }, []);

      this.setState({ people, keys }, resolve);
    });

  addDataToState = data =>
    new Promise(resolve => {
      this.setState({ data }, resolve);
    });

  async maybeProcessReceivedData({ options }) {
    if (
      this.state.data.length ||
      !options.data ||
      !options.data.carbon_fields ||
      !options.data.carbon_fields
    ) {
      return;
    }

    await this.addDataToState(options.data.carbon_fields.whos_coming__data);
    this.addPeopleToState();
  }

  render = ({ people = [] } = this.state) =>
    people.length ? people.map(person => <Person {...person} />) : null;
}

export default WhosComing;



